@import '../theme';

@prefix-cls: ~'@{ra-lib-prefix}-operator';

.@{prefix-cls} {
    &-divider {
        display: inline-block;
        margin: 0 8px;
        width: 1px;
        height: 18px;
        vertical-align: middle;
        background: #d9d9d9;
    }

    &-label {
        display: inline-block;
        background-color: transparent;
        text-decoration: none;
        outline: none;
        cursor: pointer;
        transition: color .3s;

        &:hover {
            opacity: 0.6;
        }

        &.disabled {
            cursor: not-allowed;

            &:hover {
                opacity: 1;
            }
        }
    }
}

@layout-header-background: #001529;@menu-dark-inline-submenu-bg: #000c17;@body-background: #f0f2f5;